  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../../fonts/SFProDisplay-Regular.woff2") format("woff2"), url("../../fonts/SFProDisplay-Regular.woff") format("woff"); }

@font-face {
  font-family: 'SF Pro Display';
  font-style: normal;
