*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  scroll-behavior: smooth; }

body {
  width: 100%;
  height: 100%;
  font-family: 'SF Pro Display';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #1C4F94;
  text-decoration: none; }

button {
  cursor: pointer;
  border: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input,
textarea {
  border-radius: 0; }
  input::-moz-placeholder, textarea::-moz-placeholder {
    opacity: 1; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
          appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  -webkit-appearance: none;
          appearance: none; }

input[type="number"] {
  -webkit-appearance: textfield;
     -moz-appearance: textfield;
          appearance: textfield; }

select,
textarea,
input:matches(
[type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]
) {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }

.container {
  max-width: 1274px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px; }

.inner {
  background-color: #F4F6FD;
  position: relative; }
  .inner:before {
    background-image: url("../images/desktop/bg-block.svg");
    content: '';
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: 100vw auto;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0; }
  .inner--gradient {
    background: linear-gradient(115.19deg, #1C83FF -14.66%, #751E94 100.36%); }
    .inner--gradient:before {
      background-image: url("../images/desktop/bg-block.svg"); }
  .inner__content {
    position: relative;
    display: flex;
    align-items: center;
    z-index: 1; }
  .inner__title {
    font-family: 'Geometria';
    font-size: 32px;
    line-height: 1.25;
    font-weight: 700; }
    .inner__title--gradient {
      background: linear-gradient(262.03deg, #801cff 37.71%, rgba(255, 255, 255, 0) 97.01%), linear-gradient(0deg, #014ca7, #014ca7);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
  .inner__subtitle {
    font-weight: 1.30;
    font-size: 24px;
    color: #1C4F94;
    font-weight: 700;
    margin-bottom: 30px; }
  .inner__text {
    max-width: 554px; }
  .inner__list {
    list-style: none; }
  .inner__item {
    display: flex;
    align-items: center; }
  .inner__item-text {
    color: #1C4F94;
    font-size: 18px;
    line-height: 1.3; }
    .inner__item-text--white {
      color: #fff; }
    .inner__item-text--black {
      color: #111; }
    .inner__item-text--opacity {
      opacity: 0.6; }
  .inner__picture {
    display: block; }

.button {
  font-family: "Geometria";
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  font-size: 18px;
  line-height: 1.30;
  font-weight: 400;
  cursor: pointer; }
  .button--request {
    margin-left: auto;
    background: #EEF5FF;
    color: #014ca7; }
  .button--home {
    color: #fff;
    max-width: 550px;
    width: 100%;
    background: linear-gradient(0deg, #1C83FF, #1C83FF), #1C83FF;
    border-radius: 15px;
    margin: 0 auto;
    filter: drop-shadow(0px 4px 48px rgba(25, 157, 231, 0.5)); }
  .button__image {
    margin-right: 20px;
    width: 40px;
    height: 26px; }

.header {
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 4px 23px rgba(28, 131, 255, 0.1);
  -webkit-backdrop-filter: blur(13px);
